<template>
<v-container-fluid>
  <v-layout row wrap>
    <v-flex xs-12>
      <v-card hover class="ma-2 pa-2">
        <v-card-title class="display-2">
          <v-flex>
            <p class="text-xs-center">Kendeda Event Calendar</p>
            <p class="text-xs-center display-1">{{calendarMonth + ' ' + calendarYear}}</p>
          </v-flex>
        </v-card-title>
        <v-calendar ref="calendar" type="month" v-model="calendarStart" color="primary" v-on:click:date="getDate" v-on:click:day="dayClick">
          <template v-slot:day="{ date }">
            <template v-for="event in calendarEventsMap.get(date)">
              <v-menu
                :key="event.title"
                v-model="event.open"
                full-width
                offset-x
              >
                <template v-slot:activator="{ on }">
                  <v-card full-width dark class="text-xs-left my-1 mx-0 pa-0" color='primary' hover v-if="!event.time" v-ripple v-on="on">
                    {{event.title}}
                  </v-card>
                </template>
                <v-card
                  color="grey lighten-4"
                  min-width="350px"
                  flat
                >
                  <v-toolbar
                    color="primary"
                    dark
                  >
                    <v-btn icon>
                      <v-icon>edit</v-icon>
                    </v-btn>
                    <v-tooltip top nudgeTop="3">
                      <template v-slot:activator="tooltip">
                        <v-btn icon v-on="tooltip.on" @click.stop="addEvent=true"><v-icon>add</v-icon></v-btn>
                      </template>
                      <span>Add New Event</span>
                    </v-tooltip>
                    <v-dialog v-model="addEvent" persistent max-width="600px">
                      <v-card>
                        <v-card-title>
                          <span class="headline">New Event</span>
                        </v-card-title>
                        <v-card-text>
                          <v-container grid-list-md>
                            <v-layout wrap>
                              <v-flex xs12>
                                <v-text-field
                                  v-model="newEvent.title"
                                  label="Title"
                                  required
                                ></v-text-field>
                              </v-flex>
                              <v-flex xs12>
                                <v-text-field box
                                  v-model="newEvent.details"
                                  label="Details"
                                ></v-text-field>
                              </v-flex>
                              <v-flex xs12 sm6>
                                <v-select
                                  :items="size"
                                  v-model="newEvent.people"
                                  label="Event Size"
                                  single
                                  required
                                ></v-select>
                              </v-flex>
                            </v-layout>
                          </v-container>
                        </v-card-text>
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn color="blue darken-1" flat @click="addEvent = false">Close</v-btn>
                          <v-btn color="blue darken-1" flat @click="addNewEvent(), addEvent = false">Add</v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                    <v-toolbar-title v-html="event.title"></v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn icon>
                      <v-icon>close</v-icon>
                    </v-btn>
                  </v-toolbar>
                  <v-card-title primary-title>
                    <p v-html="event.details"></p>
                  </v-card-title>
                  <v-card-text>
                    <p>Time: </p>
                    <p>Expected Number of People: {{event.people}}</p>
                    <!-- <span v-html="event.people"></span -->
                  </v-card-text>
                </v-card>
              </v-menu>

            </template>
          </template>
        </v-calendar>
        <v-card-actions>
          <v-flex xs6 class="text-xs-left">
            <v-btn color="primary" @click="$refs.calendar.prev(),getCalendarMonth(),getCalendarYear()"><v-icon>chevron_left</v-icon>Prev</v-btn>
          </v-flex>
          <v-flex xs6 class="text-xs-right">
            <v-btn color="primary" @click="$refs.calendar.next(),getCalendarMonth(),getCalendarYear()">Next<v-icon>chevron_right</v-icon></v-btn>
          </v-flex>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</v-container-fluid>
</template>

<script>
export default{
  data: () => ({
    primaryColor: 'primary',
    calendarStart: '',
    calendarMonth: new Date().toLocaleString('en-us', { month: 'long' }),
    calendarYear: new Date().getFullYear(),
    calendarMap: new Map(),
    dayList: false,
    addEvent: false,
    size: ["Small","Medium","Large"],
    intensity: ['Low','Moderate','High'],
    newEventBtnHover: false,
    newEvent: {
      title: null,
      details: null,
      date: null,
      people: 0,
      open: false,
    },
    calendarEvents: [
        {
          title: 'Company Event',
          details: 'Recruitment event for internships',
          date: '2019-03-30',
          people: '50',
          open: false
        },
        {
          title: 'Student Club Fundraiser',
          details: 'Student hosted event to raise money for club',
          date: '2019-03-31',
          people: '80',
          open: false
        },
        {
          title: 'Award Ceremony',
          details: 'Department teaching and research award reception',
          date: '2019-04-01',
          people: '100',
          open: false
        },
        {
          title: 'Georgia Tech Foundation Gala',
          details: 'Fundraising event for the GT Foundation',
          date: '2019-03-07',
          people: '150',
          open: false
        },
        {
          title: 'Research Meetings with Sponsors',
          details: 'Organizations who sponsor research receive presentations on projects',
          date: '2019-03-03',
          people: '25',
          open: false
        },
        {
          title: 'Grad Student Appreciation',
          details: 'Reception for grad students and their advisors',
          date: '2019-03-01',
          people: '75',
          open: false
        },
        {
          title: 'Conference',
          details: 'Small conference for undergraduate research',
          date: '2019-03-21',
          people: '250',
          open: false
        },
        {
          title: 'Hackathon',
          details: 'Code like there is no tommorrow',
          date: '2019-03-01',
          people: '100',
          open: false
        },
        {
          title: 'Hackathon',
          details: 'Code like there is no tomorrow',
          date: '2019-03-02',
          people: '100',
          open: false
        }
      ],
  }),
  methods: {
    addNewEvent(){
      if(this.newEvent.people === "Small"){
        this.newEvent.people = 25;
      }else if(this.newEvent.people === "Medium"){
        this.newEvent.people = 50;
      }else{
        this.newEvent.people = 75;
      }
      this.calendarEvents.push(this.newEvent);
      //this.addToCalendarEventsMap(this.newEvent);
      this.newEvent = {
        title: null,
        details: null,
        date: null,
        people: 0,
        open: false,
      };
      console.log('New Event Added!')
    },
    getDate: function(event){
      this.newEvent.date = event.date;
      this.addEvent=true;
      console.log("Adding a new event: " + this.addEvent)
    },
    dayClick: function(event){
      if(!this.calendarMap.has(event.date)){
        this.dayList = true;
        this.getDate(event)
      }
      console.log(this.dayList)
    },
    getCalendarYear(){
      let dateObject = new Date();
      if(this.calendarStart !== ''){
        dateObject = new Date(this.calendarStart.replace("-","/"));
      }
      this.calendarYear = dateObject.getFullYear();
    },
    getCalendarMonth(){
      let dateObject = new Date();
      if(this.calendarStart !== ''){
        dateObject = new Date(this.calendarStart.replace("-","/"));
      }
      this.calendarMonth = dateObject.toLocaleString('en-us',{ month: 'long' });
    },
    addToCalendarEventsMap(e){
      if(this.calendarMap.has(e.date)){
        let value = this.calendarMap.get(e.date);
        if(e==value[0]){
        }else{
          value = [value[0],e];
          this.calendarMap.set(e.date,value)
        }
      }else{
        this.calendarMap.set(e.date,[e])
      }
    },
  },
  computed: {
    calendarEventsMap () {
        this.calendarEvents.forEach(this.addToCalendarEventsMap)
        return this.calendarMap
    },
  },
}
</script>

